import React, { useEffect } from 'react';
import Typewriter from 'typewriter-effect';

export default function About() {
  useEffect(() => {
    // Code  excuter aprs le rendu
  }, []);

  return (
    <section id="about" className="scrolled">
      <h2 className="subtitle-section">INTRODUCTION</h2>
      <h3 className="title-section">A propos</h3>
      <div className="about-section">
        <img src="./assets/images/photoprofil.png" className="about-photo" alt="photoprofil" />
        <p className="about-text">
            <span className='bonjour'>
            <Typewriter
              options={{
                strings: ['Bonjour, je suis Sarah, dveloppeuse web full stack !'],
                autoStart: true,
                loop: true,
                delay: 70,
                deleteSpeed: 70
              }}
            />
            </span>
            <span>
                Mon parcours dans le monde du code a dbut en autodidacte, c'est en 2023 que j'ai formalis mes comptences en suivant une formation sur OpenClassrooms. 
                Cette exprience m'a permis d'acqurir des connaissances solides en 
                dveloppement FrontEnd et BackEnd, ainsi qu'en gestion de projet et optimisation 
                de code.
            </span>
            <span>
                Passionne par la cration, j'aime donner vie  des ides en crant des 
                sites web  la fois fonctionnels et esthtiquement plaisants. Mon objectif 
                 est d'largir mes connaissances et comptences en relevant de nouveaux 
                dfis. En dehors du code, je nourris une passion pour l'illustration 
                et la crativit.
            </span>
        </p>
      </div>
    </section>
  )
}
